td.val-error span {
  position: relative;
  color: red; }
  td.val-error span::before, td.val-error span::after {
    content: "";
    display: block;
    position: absolute;
    width: 0.75em;
    height: 2px;
    top: 50%;
    right: -2rem;
    transform: translate(0, -50%) rotate(-45deg);
    background: red; }
  td.val-error span::after {
    transform: translate(0, -50%) rotate(45deg); }

